.GotoTop{
    position: fixed;
    z-index: 999;
    bottom: 40px;
    right: 40px;
    width: 40px;
    height: 40px;
    background-color: rgb(153, 192, 212);
    border: none;
    border-radius: 6px;
    cursor: pointer;
    color: #fff;
    outline: none;
}
.Show{
    display: none;
}
